.detail {
    display: grid;
    grid-template-columns: 40% auto;
    padding: 30px 0;
    .poster {
        text-align: center;
        img {
            border: 5px solid #fff;
        }
    }
    .info {
        p {
            margin: 20px 0;
        }
        .title {
            font-size: 24px;
            font-weight: bold;
            margin: 60px 0 30px;
        }
        
    }
}