.loadWrap {
    font-size: 50px;
    min-height: 600px;
    text-align: center;
    line-height: 400px;
    .loadicon {
        font-size: 50px;
        color: #fff;
        animation: loadspin infinite 3s linear;
    }
    @keyframes loadspin {
        from {transform: rotate(0deg);}
        to {transform: rotate(360deg);}
    }
}