.header {
    height: 300px;
    border-bottom: 2px solid mediumturquoise;
    text-align: center;
    .header_title {
        color: mediumturquoise;
        padding: 30px 0;
    }
    p {
        color: #eee;
    }
    .header_info {
        padding: 20px 0 40px;
    }
    .header_important {
        color: lightpink;
    }
}

.detailLnk {
    text-decoration: none;
}
.container {
    .cf:after {
        content: '';
        clear: both;
        display: block;
    }
    h1 {
        margin: 30px 0;
        font-size: 30px;
        color: mediumturquoise;
    }
    .movies {
        width: 100%;
        // float 방식
        // .movies_list {
        //     float: left;
        //     width: 24%;
        //     text-align: center;
        //     padding: 0 0.5%;
        // }

        // flex 방식
        // .movie_list {
        // }

        // grid 방식
        display: grid;
        grid-template-columns: 23% 23% 23% 23%;
        column-gap: 2%;
        
        
        ul{
            display: grid;
            grid-template-columns: auto auto auto auto;
            li {
                padding: 0 10px;
                list-style: none;
            }
        }
        
        img {
            border: 5px solid #fff;
            width: 100%;
        }
        h3 {
            font-size: 15px;
            margin: 10px 0;
            color: #fff;
            text-decoration: none;
        }
        h5 {
            color: #fff;
            text-decoration: none;
            margin-bottom: 10px;
        }
        ul {
            li {
                margin: 5px 0;
                padding: 5px 0;
                text-align: center;
                font-weight: bold;
                font-size: 14px;
                background: mediumturquoise;
                color: #fff;
                text-decoration: none;
            }
        }
        p {
            font-size: 13px;
            display:-webkit-box; 
            word-wrap:break-word; 
            -webkit-line-clamp:3; 
            -webkit-box-orient:vertical; 
            overflow:hidden; 
            text-overflow:ellipsis;
            height: 50px;
            margin-bottom: 50px;
            text-align: left;
            color: #fff;
            text-decoration: none;
        }
    }
    
}

.footer {
    text-align: center;
    border-top: 2px solid mediumturquoise;
    ul {
        width: 70%;
        display: grid;
        grid-template-columns: auto auto auto auto auto auto auto auto auto auto auto;
        margin: 20px auto 10px;
        list-style: none;
        li {
            a {
                color: #ccc;
                text-decoration: none;
                font-size: 12px;
            }
        }
    }
    p {
        font-size: 13px;
    }
}